@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&family=Yomogi&display=swap");

.linkContainer {
  position: fixed;
  background: rgba(42, 42, 43, 0.87);
  width: 100vw;
  height: 8rem;
  right: 2rem;
  z-index: 9999;
}

.linkNav {
  display: inline;
  color: rgb(245, 242, 176);
  margin-right: 2rem;
  float: right;
  font-size: 1.2rem;
  z-index: 1;
  padding: 1rem;
  line-height: 6rem;
  transition: all 0.5s ease;
  font-family: "Roboto Mono", monospace;
}

.linkNav:hover {
  background: rgb(46, 46, 46);
  transform: scale(0.97);
}

.logoNume {
  position: absolute;
  display: inline;
  color: rgb(245, 242, 176);
  text-decoration: none;
  font-size: 2rem;
  font-family: "Roboto Mono", monospace;

  left: 15rem;
  line-height: 6rem;
  padding: 1rem;
  transition: all 0.5s ease;
}

.logoNume:hover {
  background: rgb(46, 46, 46);
}

.containerVideo {
  position: absolute;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 130%;
  background: #888d5f;
  mix-blend-mode: overlay;
}

.bigText {
  position: relative;
  color: rgb(141, 140, 101);
  font-size: 9rem;
  margin: auto;
  height: 500px;
  max-width: fit-content;
  margin-bottom: 35rem;
}

.bigText h1 {
  position: absolute;
  top: 2.5rem;
  left: -7rem;
  background: rgba(109, 106, 106, 0.295);
  opacity: 0;
  animation: revealName 3s ease forwards;
  font-family: "Roboto Mono", monospace;
  line-height: 20rem;
}

@keyframes revealName {
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.section2 {
  position: absolute;
  width: 100%;
  background: rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
}

.slideContainer0 {
  position: absolute;
  top: 0;
  left: 10rem;
  font-size: 3rem;
  text-transform: uppercase;
  font-family: "Roboto Mono", monospace;
  letter-spacing: 1px;
  color: rgb(141, 140, 101);
}

.slideContainer1 {
  position: absolute;
  top: 10rem;
  right: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slideContainer1 img {
  width: 400px;
}

.slideContainer1 h1 {
  margin-top: 25rem;
  color: rgb(141, 140, 101);
  text-transform: uppercase;
  font-family: "Roboto Mono", monospace;
  letter-spacing: 1px;
  font-size: 3rem;
}

.teamplayer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 60rem;
  margin-left: -60rem;
  padding-bottom: 40rem;
}

.teamplayer img {
  position: relative;
  width: 700px;
  padding-top: 5rem;
}

.teamplayer img::before {
  position: absolute;
	top: 0;
	left: -100%;
	z-index: 2;
	display: block;
	content: '';
	width: 60%;
	height: 100%;
	background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
	background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
	-webkit-transform: skewX(-25deg);
	transform: skewX(-25deg);
}

.teamplayer img:hover::before {
  -webkit-animation: shine .35s;
	animation: shine .35s;
}
@keyframes shine {
	100% {
		left: 100%;
	}
}


.teamplayer h1 {
  position: absolute;
  top: 0;
  color: rgb(141, 140, 101);
  font-family: "Roboto Mono", monospace;
}

.slider {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  bottom: -20rem;
  margin-bottom: 10rem;
  width: 90%;
  transition: all 0.4s ease;
}
.titlu {
  color: rgb(141, 140, 101);
  font-family: "Roboto Mono", monospace;
  margin-bottom: 5rem;
}

.slideTrack {
  display: flex;
  justify-content: center;
  width: calc(200px * 18);
  animation: scroll 30s linear infinite;
}
.slideTrack:hover {
  animation-play-state: paused;
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-200px * 9));
  }
}
.slide {
  transition: all 0.3s ease;
}

.slide:hover {
  transform: scale(1.5);
}

.slideTrack img {
  height: 160px;
  padding: 2rem;
  margin-bottom: 10rem;
}

