
body,
html {
  margin: 0;
  background: rgb(0, 0, 0);
  background-size: cover;
  background-repeat: no-repeat;
  overflow-x: hidden;
  box-sizing: border-box;
  height: 100vh;
}

.linkContainer {
  position: absolute;
  top: 0;
  background: rgba(42, 42, 43, 0.87);
  width: 100vw;
  height: 8rem;
  right: 2rem;
  z-index: 9999;
  /* margin-right: 1rem; */
}

.linkNav {
  display: inline;
  color: rgb(245, 242, 176);
  margin-right: 2rem;
  float: right;
  font-size: 1.2rem;
  z-index: 1;
  padding: 1rem;
  line-height: 6rem;
  transition: all 0.5s ease;
  font-family: "Roboto Mono", monospace;
}

.linkNav:hover {
  background: rgb(46, 46, 46);
  transform: scale(0.97);
}

.logoNume {
  position: absolute;
  display: inline;
  color: rgb(245, 242, 176);
  text-decoration: none;
  font-size: 2rem;
  font-family: "Roboto Mono", monospace;

  left: 15rem;
  line-height: 6rem;
  padding: 1rem;
  transition: all 0.5s ease;
}

.logoNume:hover {
  background: rgb(46, 46, 46);
}

.svg {
  position: absolute;
  pointer-events: none;
}
.svg2 {
  position: absolute;
  pointer-events: none;
}

a {
  text-decoration: none;
}

.link {
  margin-top: 2rem;
}

ul {
  list-style-type: none;
  margin: 10% auto 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-gap: 20px;
  width: 80%;
  height: 70%;
}

ul a {
  color: rgba(127, 255, 212, 0.671);
  display: block;
  font-weight: bold;
  font-size: 2em;
  text-transform: uppercase;
  transition: all 0.3s ease-in;
}

.link:hover {
  display: block;
  color: rgb(127, 255, 212);
  transform: scale(1.1);
  transition: all 0.3s ease-out;
  text-shadow: 3px 3px 1rem aquamarine;
}
ul span {
  color: rgba(255, 255, 255, 0.671);
  display: block;
  font-size: 1em;
  letter-spacing: 2px;
  margin: 100px auto;
  width: 80%;
  text-align: center;
}

.bottom {
  position: absolute;
  width: 100%;
  bottom: 0;
  text-align: center;
  line-height: 100px;
  color: rgba(252, 252, 252, 0.671);
  height: 100px;
  background: rgba(127, 255, 212, 0.014);
}
ul li {
  border: 1px solid rgba(127, 255, 212, 0.288);
  opacity: 0;
  border-radius: 10px;
  text-align: center;
  padding-top: 2em;
  padding-bottom: 4rem;
  background: rgba(189, 199, 199, 0.103);
  box-shadow: 2px 2px 1rem rgba(187, 202, 202, 0.411);
  transition: all 0.3s ease-in-out;
  overflow: hidden;
}

ul li:hover {
  box-shadow: -4px -4px 0.5rem rgb(90, 89, 89);
  transition: all 0.3s ease-in-out;
}
ul li:nth-last-of-type(1) {
  animation: menu-item-animation 0.6s forwards 1s ease-in-out;
}
ul li:nth-last-of-type(2) {
  animation: menu-item-animation 0.6s forwards 1.2s ease-in-out;
}
ul li:nth-last-of-type(3) {
  animation: menu-item-animation 0.6s forwards 1.4s ease-in-out;
}
ul li:nth-last-of-type(4) {
  animation: menu-item-animation 0.6s forwards 1.6s ease-in-out;
}

.show-menu {
  display: block;
  animation: slide-meniu 1s ease-in forwards;
}

.extra {
  position: absolute;
  width: 100px;
  height: 100px;
  background: aquamarine;
  top: 0;
  right: -30px;
  transform: skew(45deg);
}

.star {
  position: absolute;
  top: 10px;
  right: 5px;
  width: 50px;
  transform: rotate(45deg);
}

.center-text {
  color: white;
  font-size: 2rem;
  text-align: center;
  font-family: sans-serif;
}
.center-text h1 {
  width: 70%;
  margin: auto;
}

.menuText {
  color: rgb(197, 194, 130);
  display: inline-block;
  transform: rotate(270deg);
  position: absolute;
  top: 40%;
  font-size: 5rem;
  left: -10%;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-weight: 500;
  font-family: sans-serif;
  transition: all 0.2s ease;
}
.menuText span:hover {
  line-height: 2rem;
  display: inline-block;
  transform: rotate(90deg);
  transition: all 0.2s ease;
  text-shadow: 3px 3px 2rem aquamarine;
}

