  width: 10rem;
  left: 5%;
  top: 5%;
} */

@keyframes slide-meniu {
  from {
    transform: scaleX(0);
  }

  to {
    transform: scaleX(1);
  }
}

@keyframes show-button {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes menu-item-animation {
  from {
    transform: translateY(60%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@media screen and (max-width: 1300px) {
  .menuText {
    display: block;
    position: absolute;
    top: 2%;
    left: 40%;
    transform: rotate(360deg);
    font-size: 3rem;
  }
  .menuText span:hover {
    display: inline-block;
    transform: rotate(0deg);
  }
}

@media screen and (max-width: 900px) {
  ul {
    position: absolute;
    top: 10%;
    display: grid;
    grid-template-columns: 1fr;
  }
  .menuText {
    display: block;
    position: absolute;
    top: 4%;
    left: 30%;
    transform: rotate(360deg);
    font-size: 2rem;
  }
  .center-text h1 {
    font-size: 2rem;
  }
  section h1 {
    font-size: 2rem;
  }
  .menuText span:hover {
    display: inline-block;
    transform: rotate(0deg);
  }
}
@media screen and (max-width: 450px) {
  ul {
    position: absolute;
    top: 5%;
    display: grid;
    grid-template-columns: 1fr;
    padding-top: 40%;
  }
  .menuText {
    display: block;
    position: absolute;
    top: 10%;
    left: 20%;
    transform: rotate(360deg);
    font-size: 2rem;
  }
  .center-text h1 {
    font-size: 2rem;
  }
  section h1 {
    font-size: 2rem;
  }
  .menuText span:hover {
    display: inline-block;
    transform: rotate(0deg);
  }
}
