@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&family=Yomogi&display=swap");

.linkContainer {
  position: fixed;
  background: rgba(42, 42, 43, 0.87);
  width: 100vw;
  height: 8rem;
  right: 2rem;
  z-index: 9999;
}

.linkNav {
  display: inline;
  color: rgb(245, 242, 176);
  margin-right: 2rem;
  float: right;
  font-size: 1.2rem;
  z-index: 1;
  padding: 1rem;
  line-height: 6rem;
  transition: all 0.5s ease;
  font-family: "Roboto Mono", monospace;
}

.linkNav:hover {
  background: rgb(46, 46, 46);
  transform: scale(0.97);
}

.logoNume {
  position: absolute;
  display: inline;
  color: rgb(245, 242, 176);
  text-decoration: none;
  font-size: 2rem;
  font-family: "Roboto Mono", monospace;

  left: 15rem;
  line-height: 6rem;
  padding: 1rem;
  transition: all 0.5s ease;
}

.logoNume:hover {
  background: rgb(46, 46, 46);
}

/* data */
.nasaMasterContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: calc(100vh - 8rem);
  /* background: yellow; */
  position: absolute;
  top: 8rem;
  grid-gap: 2rem;
}

.nasaMasterContainer2 {
  display: flex;
  justify-content: center;
  width: 80%;
  height: calc(100vh - 8rem);
  /* background: yellow; */
  position: absolute;
}

.nasaPhotoContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 2rem;
}

.nasaPhotoContainer img {
  width: 900px;
  border-radius: 10px;
}

.nasaTextContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.nasaTextContainer p:nth-of-type(1){
  color: rgb(240, 236, 156);
  
}

.nasaTextContainer p:nth-of-type(2) {
  width: 80%;
  font-family: "Roboto Mono";
  font-weight: bolder;
  line-height: 25px;
  font-size: 18px;
  color: rgb(240, 236, 156);
  text-align: center;
  border: 1px solid rgb(240, 236, 156);
  padding: 2rem;
}

.nasaTextContainer h1 {
  text-align: center;
  /* max-width: 600px; */
  letter-spacing: 2px;
  color: rgb(240, 236, 156);
  font-size: 2rem;
  border-bottom: 1px solid yellow;
  animation: animateBorder 2s forwards;
  font-family: "Roboto Mono", monospace;
  transition: all .3s ease-in-out;
}
.clasaTitlu {
  margin-top: 10rem;
  text-transform: capitalize;
}

@keyframes animateBorder {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.explicatieNasa {
  color: yellow;
  position: absolute;
  bottom: 0;
  right: 10rem;
}



.nasaExplicatieButon {
  width: max-content;
  background: transparent;
  outline: none;
  border: none;
  position: absolute;
  right: 3rem;
  bottom: 1rem;
  transition: all .2s ease-in-out;
}

/* animation button */
.nasaExplicatieButon:link,
.nasaExplicatieButon:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 15px 40px;
    display: inline-block;
    border-radius: 100px;
    transition: all .2s;
    position: absolute;
}

.nasaExplicatieButon:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.nasaExplicatieButon:active {
    transform: translateY(-1px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.nasaExplicatieButonWhite {
    /* background-color: #fff; */
    color: #777;
}

.nasaExplicatieButon::after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all .4s;
}

.nasaExplicatieButonWhite::after {
    background-color: #fff;
}

.nasaExplicatieButon:hover::after {
    transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
}

.nasaExplicatieButonAnimated {
    animation: moveInBottom 5s ease-out;
    animation-fill-mode: backwards;
}

@keyframes moveInBottom {
    0% {
        opacity: 0;
        transform: translateY(30px);
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}
/* end animation */



.nasaExplicatieButon img {
  width: 40px;
}