@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&family=Yomogi&display=swap");
.main {
  padding: 0;
  box-sizing: border-box;
  height: auto;
  width: 100vw;
  padding-bottom: 5rem;
  background: url('../img/pattern8.jpg');
}

/* nav */

.linkContainer {
  position: fixed;
  top: 0;
  background: rgba(42, 42, 43, 0.87);
  width: 100vw;
  height: 8rem;
  right: 2rem;
  z-index: 9999;
}

.linkNav {
  display: inline;
  color: rgb(245, 242, 176);
  margin-right: 2rem;
  float: right;
  font-size: 1.2rem;
  z-index: 1;
  padding: 1rem;
  line-height: 6rem;
  transition: all 0.5s ease;
  font-family: "Roboto Mono", monospace;
}

.linkNav:hover {
  background: rgb(46, 46, 46);
  transform: scale(0.97);
}

.logoNume {
  position: absolute;
  display: inline;
  color: rgb(245, 242, 176);
  text-decoration: none;
  font-size: 2rem;
  font-family: "Roboto Mono", monospace;

  left: 15rem;
  line-height: 6rem;
  padding: 1rem;
  transition: all 0.5s ease;
}

.logoNume:hover {
  background: rgb(46, 46, 46);
}
/* end nav */

.mainHeader {
  text-align: center;
  margin-top: 8rem;
  padding-top: 2rem;
  color: rgb(247, 248, 171);
  font-family: "Roboto Mono", monospace;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 3rem;
  font-weight: bold;
}
