
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&family=Yomogi&display=swap");

.linkContainer {
  position: fixed;
  background: rgba(42, 42, 43, 0.87);
  width: 100vw;
  height: 8rem;
  right: 2rem;
  z-index: 9999;
}

.linkNav {
  display: inline;
  color: rgb(245, 242, 176);
  margin-right: 2rem;
  float: right;
  font-size: 1.2rem;
  z-index: 1;
  padding: 1rem;
  line-height: 6rem;
  transition: all 0.5s ease;
  font-family: "Roboto Mono", monospace;
}

.linkNav:hover {
  background: rgb(46, 46, 46);
  transform: scale(0.97);
}

.logoNume {
  position: absolute;
  display: inline;
  color: rgb(245, 242, 176);
  text-decoration: none;
  font-size: 2rem;
  font-family: "Roboto Mono", monospace;

  left: 15rem;
  line-height: 6rem;
  padding: 1rem;
  transition: all 0.5s ease;
}

.logoNume:hover {
  background: rgb(46, 46, 46);
}

.contactBackground {
  position: relative;
  height: 100vh;
  width: 100vw;
  background: url("../img/back1.jpg");
  /* mix-blend-mode: luminosity; */
}

.masterDiv {
display: grid;
grid-template-columns: 1fr 2fr;
height: 70vh;
width: 90vw;
background: rgba(3, 2, 2, 0.479);
border: 1px solid rgba(255, 252, 80, 0.479);
margin: auto;
transform: translateY(30%);
grid-gap: 3rem;
}

/* tiles */
.containerTiles {
  display: grid;
  width: 35vw;
  height: 65vh;
  grid-template-columns: 1fr 1fr 1fr;
  padding-top:  4rem;
  padding-left: 2rem;
 
}

.div {
  background: url("../img/io2.jpg") no-repeat ;
  background-position:left;
  background-attachment: fixed;
  background-size: 900px;
}

.div1 {
  opacity: 0;
  
}
