/* end tiles */


.containerText {
  width: 50%;
  height: 60%;
   display: flex;
   justify-content: center;
   align-items: left;
   flex-direction: column;
   padding: 0 5rem;
   transform: translateY(25%);
   font-family: sans-serif;
}

.containerText h1 {
  color: white;
  letter-spacing: 2px;
  font-size: 3rem;
  border-bottom: 1px solid yellow;
  animation: animateBorder 2s forwards;
  font-family: "Roboto Mono", monospace;
}

@keyframes animateBorder {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.containerText p:nth-of-type(1) {
  color: white;
  font-family: sans-serif;
  font-size: 20px;
  font-weight: bold;
  text-transform: capitalize;
  letter-spacing: 2px;
  font-family: "Roboto Mono", monospace;
}

.qulificationStar {
  position: absolute;
  top:10px;
  right: 16rem;
  width: 60px;
  transform: rotate(25deg);
  transition: all .5s ease;
}
.qulificationStar:hover {
  transform: rotate(-25deg) scale(1.04);  
}

.containerText p:nth-of-type(2) {
  color: white;
  line-height: 25px;
  letter-spacing: 1px;
  padding-bottom: 3rem;
  font-family: "Roboto Mono", monospace;
  font-weight: bolder;
}

.containerText button {
  width: 20rem;
  padding: .5rem 1rem;
  background: rgba(255, 255, 0, 0.158);
  color: yellow;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1rem;
  transition: all .4s ease;
  border-radius: 5px;
  font-weight: bold;
  outline: none;
}

.containerText button:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 0, 0.212);
}

.contactando {
  color: white;
  font-family: "Roboto Mono", monospace;
  font-size: 1.3rem;
  padding-top: 5rem;
}

.socialContactIcons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr ;
  margin-top: 2rem;
  margin-left: 15rem;
}

.social1 img {
  width: 40px;
  transition: all .4s ease-in-out;
}
.social1 img:hover {
   transform: scale(1.1);
}

.social2 img {
  width: 40px;
  transition: all .4s ease-in-out;
}
.social2 img:hover {
  transform: scale(1.1);
}
.social3 img {
  width: 40px;
  background: transparent;
  transition: all .4s ease-in-out;
}
.social3 img:hover {
  transform: scale(1.1);
}
.social4 img {
  width: 40px;
  transition: all .4s ease-in-out;
}
.social4 img:hover {
  transform: scale(1.1);
}