@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&family=Yomogi&display=swap");

.containerr {
  position: relative;
  width: 90vw;
  margin: 5rem auto;
  background-color: rgb(214, 214, 214);

  border-radius: var(0.25rem);
  padding: 2.5rem 2rem;
  max-width: 920px;
  display: grid;
  gap: 1rem 2rem;
  border-radius: 10px 0 10px 0;
  transition: all 0.4s ease;
  box-shadow: 0px 0px 0.5rem rgb(230, 230, 230);
}
.containerr:hover {
  box-shadow: 0px 0px 1rem rgb(228, 227, 227);
}

.sageata {
  clip-path: polygon(100% 0, 95% 20%, 100% 40%, 10% 40%, 5% 20%, 0 20%, 5% 0);
  position: absolute;
  top: -1rem;
  right: -2rem;
  width: 580px;
  height: 80px;
  /* background: rgb(218, 85, 112); */
  animation: colorChange 10s infinite linear;
}

.sageata p {
  font-size: 1.3rem;
  color: rgb(251, 255, 0);
  margin-left: 5rem;
  margin-top: 13px;
  line-height: 0px;
  letter-spacing: 1px;
  font-family: "Roboto Mono";
}
@keyframes colorChange {
  0% {
    background-color: #6666ff;
  }
  20% {
    background-color: #4d4dff;
  }
  40% {
    background-color: #3333ff;
  }
  60% {
    background-color: #1919ff;
  }
  80% {
    background-color: #0000ff;
  }
  100% {
    background-color: #6666ff;
  }
}

.forma2 {
  position: absolute;
  width: 250px;
  height: 250px;
  left: -0.95rem;
  bottom: -0.95rem;
  animation: colorChange 10s infinite linear;
  clip-path: polygon(
    6% 22%,
    6% 38%,
    65% 94%,
    100% 94%,
    89% 100%,
    30% 100%,
    0% 70%,
    0 33%
  );
}

.projectCount {
  color: rgb(255, 255, 255);
  position: absolute;
  bottom: 0;
  left: 2.5rem;
  font-size: 3rem;
}

.containerr h2 {
  line-height: 1.2;
  font-weight: bolder;
  color: rgb(83, 82, 68);
  font-family: "Roboto Mono";
  text-align: center;
}

.btn {
  color: red;
  width: 3rem;
  height: 3rem;
  border: none;
  border-radius: 50%;
  font-size: 1.3rem;
  transition: all 0.4s ease;
  margin: auto;
}
.btn:hover {
  transform: scale(1.3);
  background: rgb(218, 85, 112);
  color: white;
}

.projectInfo {
  font-size: 18px;
  font-family: "Roboto Mono";
  font-weight: bolder;
  color: rgb(12, 12, 11);
}

.projectLink {
  float: right;
  margin: auto;
  text-decoration: none;
  font-size: 1.2rem;
  padding: 1rem;
  background: rgb(160, 160, 160);
  color: rgb(245, 242, 176);
  border-radius: 5px;
  transition: all 0.4s ease;
}
.projectLink:hover {
  background-color: rgb(218, 85, 112);
  transform: scale(1.05);
}
